footer {
  background: var(--color-bg-variant);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  padding-bottom: 6rem;
}

.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto 3rem;
  gap: 2rem;
}

.footer__socials {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
  font-size: 1.3rem;
}

.footer__logo img {
  width: 10%;
  display: inline-block;
  margin-right: 10px;
}
.footer__logo small {
  vertical-align: top;
}
.footer__logo small:hover {
  cursor: pointer;
}
/* -------- Media Queries (Small) ---------*/
@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    gap: 1.5rem;
  }
}

/* -------  Sky Background -------*/

.container__sky__background {
  overflow: hidden;
  background: transparent;
}

